<template>
  <div>
    <Modal :title="`${curRow.name}-特价`" v-model="showModal" width="800" class="specialMngModal">
      <div class="specialMng" v-if="showModal">
        <specialMng :curRow="curRow" @close="showModal = false" />
      </div>
    </Modal>
  </div>
</template>

<script>
import specialMng from "./index";

export default {
  name: "specialMngModal",
  components: { specialMng },
  props: {
    modelValue: {
      type: Boolean,
      default: false,
    },
    curRow: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  computed: {
    showModal: {
      get() {
        return this.modelValue;
      },
      set(val) {
        this.$emit("update:modelValue", val);
      },
    }
  },
};
</script>

<style lang="less" scoped>
.specialMng {
  height: 70vh;
}
</style>
<style lang="less">
.specialMngModal {
  .ivu-modal-body {
    padding: 0;
  }
}
</style>
