<template>
  <viLayoutUpDown>
    <template #up>
      <viSearchForm v-model="searchData" @search="reSearch">
        <FormItem label="客户名称:" prop="userName" v-if="!curRow.userId">
          <viInput v-model="searchData.userName"></viInput>
        </FormItem>
        <FormItem label="商品名称:" prop="goodName" v-if="!curRow.goodId">
          <viInput v-model="searchData.goodName"></viInput>
        </FormItem>
        <div class="mb-10">
          勾选数据批量调整：
          <viInputNumber v-model="step" :step="0.5" @on-enter="changeStep" class="step-input"></viInputNumber>
          <Button type="primary" @click="changeStep" class="ml-10">确认</Button>
        </div>
        <template #extral>
          <Button type="error" @click="deleteData()" class="ml-10">批量删除</Button>
        </template>
      </viSearchForm>
    </template>
    <template #down>
      <viTablePage
        ref="table"
        :columns="columns"
        :getData="getData"
        addSelect
        addIndex
        @on-selection-change="selected = $event"
      >
        <template #special="{ row, index }">
          <span class="vi-link-pointer" @click="changeIndex = index" v-if="changeIndex != index">
            ￥ {{ row.specialPrice && row.specialPrice.toFixed(2) }}
          </span>
          <div class="flex-cc" v-else>
            ￥ <viInputNumber v-model="row.specialPrice" :min="0" :step="0.5"></viInputNumber>
            <Button @click="changeIndex = -1" class="ml-10">取消</Button>
            <Button type="primary" @click="changeData([row])" class="ml-10">确认</Button>
          </div>
        </template>
        <template #operate="{ row }">
          <a @click="deleteData([row])" class="vi-red-pointer">删除</a>
        </template>
      </viTablePage>
    </template>
  </viLayoutUpDown>
</template>

<script>
import { getPricesByPage, deletePrice, savePrice } from "@/axios";

export default {
  name: "specialMng",
  props: {
    curRow: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  data() {
    return {
      searchData: {},
      selected: [],
      changeIndex: -1,
      step: 0,
    };
  },
  computed: {
    columns() {
      return [
        ...(this.curRow.userId ? [] : [{ title: "客户名称", key: "userName", align: "center" }]),
        ...(this.curRow.goodId ? [] : [{ title: "商品名称", key: "goodName", align: "center" }]),
        { title: "特价（点击修改）", slot: "special", align: "center", width: 300 },
        { title: "操作", slot: "operate", align: "center", width: 150 },
      ];
    },
  },
  methods: {
    reSearch() {
      this.$refs.table?.changePage(1);
    },
    getData(param) {
      this.selected = [];
      this.changeIndex = -1;
      return getPricesByPage({
        ...param,
        ...this.searchData,
        ...this.curRow,
      });
    },
    changeStep() {
      if (!this.selected.length) {
        return this.$Message.info("至少勾选一条数据");
      }
      this.changeData(this.selected.map((item) => {
        item.specialPrice += this.step;
        return item;
      }));
    },
    async changeData(list) {
      try {
        const { returnCode, data } = await savePrice({ list }) || {};
        if (returnCode === 0 && data) {
          this.$Message.success("保存成功");
          this.$refs.table.changePage(1);
        }
      } catch (err) {
        this.$Message.error(`保存失败：${err.message || err}`);
      }
    },
    deleteData(selected = this.selected) {
      if (!selected.length) return this.$Message.info("至少选中一项");
      this.$Modal.confirm({
        title: "删除特价",
        content: "是否确认删除所选特价？",
        onOk: async () => {
          try {
            const { returnCode, data } = await deletePrice({
              list: selected.map((item) => {
                return {
                  userId: item.userId,
                  goodId: item.goodId,
                };
              })
            }) || {};
            if (returnCode === 0 && data) {
              this.$Message.success("删除成功");
              this.$refs.table.changePage(1);
            }
          } catch (err) {
            this.$Message.error(`删除失败：${err.message || err}`);
          }
        },
      });
    },
  },
  activated() {
    this.reSearch();
  },
};
</script>

<style lang="less" scoped>
.step-input {
  width: 80px !important;
}
</style>
